<script>
import empty from "@/components/empty.vue";
export default {
  name: "DLC",
  components: {
    empty
  },
  mounted() {
    window.addEventListener('keydown', this.handleKeyDown);
  },
  beforeUnmount() {
    window.removeEventListener('keydown', this.handleKeyDown);
  },
  methods: {
    handleKeyDown(event) {
      if (event.shiftKey && event.key === 'D') {
        window.addEventListener('keydown', this.handleKeyDown2);
      }

    },
    handleKeyDown2(event) {
      if (event.shiftKey && event.key === 'L') {
        window.addEventListener('keydown', this.handleKeyDown3);
      }
      window.removeEventListener('keydown', this.handleKeyDown2);
    },
    handleKeyDown3(event) {
      if (event.shiftKey && event.key === 'C') {
        this.$router.push('/library');
      }
      window.removeEventListener('keydown', this.handleKeyDown3);
    },
  }
};
</script>

